import {useRef, useEffect} from 'react';

import {CloseIcon} from '../Icons';

import type React from 'react';
import './Modal.scss';

export type ModalProps = {
  children: React.ReactNode;
  onCloseModal: () => void;
  modalClassName?: string;
};

const Modal: React.FC<ModalProps> = ({children, onCloseModal, modalClassName = ''}) => {
  const modalRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    document.body.classList.add('body-no-scroll');

    return () => {
      document.body.classList.remove('body-no-scroll');
    };
  }, []);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const currentClickedElement = event.target as Node;
      const modalBox = modalRef.current;

      if (!modalBox) {
        return;
      }

      const isCurrentElementInsideModalBox = modalBox.contains(currentClickedElement);

      if (!isCurrentElementInsideModalBox) {
        onCloseModal();
      }
    };

    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        onCloseModal();
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    document.addEventListener('keydown', handleEscape);

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
      document.removeEventListener('keydown', handleEscape);
    };
  }, [onCloseModal]);

  return (
    <div data-testid="Modal-Box" className="Modal__box">
      <div className={`Modal__modal ${modalClassName}`} data-testid="Modal" ref={modalRef}>
        <div className="Modal__close-button-box">
          <CloseIcon
            onClick={onCloseModal}
            className="Modal__close-button"
            data-testid="Modal-CloseButton"
          />
        </div>
        {children}
      </div>
    </div>
  );
};

export default Modal;
