import r2wc from '@r2wc/react-to-web-component';

import type {ComponentType} from 'react';

interface WebComponentProps {
  [key: string]: 'string' | 'number' | 'boolean' | 'function' | 'json' | undefined;
}
const withWebComponent = <T>(
  WrappedComponent: T,
  componentAEMName: string,
  webComponentProps: WebComponentProps
): T => {
  const WebComponent = r2wc(WrappedComponent as unknown as ComponentType, {
    props: webComponentProps
  });
  window.customElements.define(componentAEMName, WebComponent);
  return WrappedComponent;
};

export default withWebComponent;
