import {useCallback, useState} from 'react';

import './MenuItem.scss';

import {useNutrition} from '../../hooks';
import Abv from '../Abv';
import Modal from '../Modal';
import Nutrition from '../Nutrition';
import Text from '../Text';

import Featured from './Featured';
import MenuItemModalContent from './MenuItemModalContent';
import PriceAndDietaryInfo from './PriceAndDietaryInfo';

import type {MenuItemProps} from './types';

// Image will be added in the future
const MenuItem = (props: MenuItemProps) => {
  const {
    guestFacingName,
    guestFacingDescriptionDigital,
    portions = [],
    isGri = false,
    claims,
    isFeaturedItem = false,
    abv,
    'data-testid': dataTestId
  } = props;

  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOnItemClick = useCallback(() => setIsModalOpen(true), []);
  const handleOnCloseModal = useCallback(() => setIsModalOpen(false), []);

  const {energyKcalPerPortion, withNutrition} = useNutrition(isGri, portions);

  return (
    <>
      {isModalOpen && (
        <Modal onCloseModal={handleOnCloseModal} modalClassName="Modal-menu-item">
          <MenuItemModalContent item={props} />
        </Modal>
      )}
      <article
        className={isFeaturedItem ? 'Item__card Item__featured' : 'Item__card'}
        data-testid={dataTestId || 'MenuItem-Card'}
        onClick={handleOnItemClick}>
        <section className="Item__details" data-testid="MenuItem-Details">
          <div className="Item__name-description-box" data-testid="MenuItem-NameAndDescriptionBox">
            <Text
              as="h2"
              className={isFeaturedItem ? 'Item__name Item__featured-name' : 'Item__name'}
              data-testid="MenuItem-Name">
              {guestFacingName}
            </Text>
            <Text
              font="secondary"
              as="p"
              className={
                isFeaturedItem
                  ? 'Item__description Item__featured-description'
                  : 'Item__description'
              }
              data-testid="MenuItem-Description">
              {guestFacingDescriptionDigital}
            </Text>
            <div className="Item__nutrition-and-abv">
              {withNutrition && energyKcalPerPortion && (
                <Nutrition
                  energyKcalPerPortion={energyKcalPerPortion}
                  data-testid="MenuItem-Nutrition"
                  isFeaturedItem={isFeaturedItem}
                  withComma={Boolean(abv)}
                />
              )}
              {abv && <Abv abv={abv} data-testid="MenuItem-Abv" isFeaturedItem={isFeaturedItem} />}
            </div>
          </div>
          <PriceAndDietaryInfo
            isVegan={claims?.madeWithVeganIngs}
            isVegetarian={claims?.madeWithVegetarianIngs}
            data-testid="MenuItem-PriceAndNutrition"
            isFeaturedItem={isFeaturedItem}
          />
        </section>
        {isFeaturedItem && (
          <div className="Item__featured-box">
            <Featured data-testid="MenuItem-Featured" />
          </div>
        )}
      </article>
    </>
  );
};

export default MenuItem;
