import MenuSubSection from '../MenuSubSection';

import Text from './../Text';

import type {IMenuSection} from './types';
import type {FC} from 'react';
import './MenuSection.scss';

const MenuSection: FC<IMenuSection> = ({
  sectionId,
  name,
  menuCopyHeader,
  // menuCopyFooter,
  subSections = [],
  menuItems = [],
  menuCopyCaption
}) => {
  const withSubsection = subSections.length > 0 && menuItems.length === 0;

  if (!withSubsection) {
    return (
      <MenuSubSection
        subSectionId={sectionId}
        name={name}
        menuCopyHeader={menuCopyHeader}
        menuItems={menuItems}
        menuCopyCaption={menuCopyCaption}
      />
    );
  }
  return (
    <div className="MenuSection__wrapper">
      <div className="MenuSection__heading-and-caption">
        <div className="MenuSection__heading MenuSection__container">
          <Text className="MenuSection__title">{menuCopyHeader}</Text>
        </div>
        {menuCopyCaption && (
          <Text as="p" font="secondary" className="MenuSection__caption">
            {menuCopyCaption}
          </Text>
        )}
      </div>
      <div className="MenuSection__container MenuSection__subsection-container">
        <div className="MenuSection__subsection-wrapper">
          {subSections.map((subsection) => (
            <MenuSubSection
              key={subsection.subSectionId}
              subSectionId={subsection.subSectionId}
              name={subsection.name}
              menuCopyHeader={subsection.menuCopyHeader}
              menuItems={subsection.menuItems}
              isSubSection={true}
              menuCopyCaption={subsection.menuCopyCaption}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default MenuSection;
