import {BrandType} from './gtm';

export interface MarketingBrand {
  Id: string;
  ScalableId: string;
  Name: string;
  MarketingBrandName: string;
  googleMapsAPIKey: string;
  googleTagManagerId: string;
  googleAnalyticsTrackingid: string;
  RegistrationEnabled: boolean;
  BrandRegistrationEnabled: boolean;
  IsHardBrand: boolean;
  Domain: string;
  facebookId: string;
  twitterUsername: string;
  instagramId: string;
  instagramUsername: string;
  myAccountEnabled: boolean;
}

export interface Address {
  Line1: string;
  Town: string;
  County: string;
  Postcode: string;
}

export interface Outlet {
  Id: string;
  ScalableId: string;
  ScalableSalesAreaId: string;
  BunCode: string;
  Name: string;
  Home: string;
  Address: Address;
  Domain: string;
  facebookId: string;
  twitterUsername: string;
  instagramId: string;
  instagramUsername: string;
  googleTagManagerIdOutlet: string;
}

export type WindowWithEstate = Window & {
  Estate: {
    MarketingBrand: MarketingBrand;
    Outlet: Outlet;
  };
};

export declare const window: WindowWithEstate;
export const getBrandType = (): BrandType => {
  return window?.Estate?.MarketingBrand?.IsHardBrand ? BrandType.HARD : BrandType.SOFT;
};

export const getOutletName = (): string => {
  return window?.Estate?.Outlet?.Name ?? 'undefined';
};

export const getBunId = (): string => {
  return window?.Estate?.Outlet?.BunCode ?? 'undefined';
};
