import DietaryIcon from '../../DietaryIcon';
import './PriceAndDietaryInfo.scss';
import Text from '../../Text';

export type PriceAndDietaryInfoProps = {
  price?: number | null;
  isVegan?: boolean;
  isVegetarian?: boolean;
  isGlutenFree?: boolean;
  isFeaturedItem?: boolean;
  isInModal?: boolean;
  'data-testid'?: string;
};

const PriceAndDietaryInfo = ({
  price,
  isVegan = false,
  isVegetarian = false,
  isGlutenFree = false,
  isFeaturedItem = false,
  isInModal = false,
  'data-testid': dataTestId
}: PriceAndDietaryInfoProps) => {
  const withIcons = isVegan || isVegetarian || isGlutenFree;
  const size = isInModal ? 'large' : 'small';
  return (
    <div className="PriceAndDietaryInfo__box" data-testid={dataTestId || 'PriceAndDietaryInfo-Box'}>
      {price && (
        <Text
          font="secondary"
          className={`PriceAndDietaryInfo__price${isFeaturedItem ? ' PriceAndDietaryInfo__featured-price' : ''}`}
          data-testid="PriceAndDietaryInfo-Price">
          £{price}
        </Text>
      )}
      {withIcons && (
        <div className="PriceAndDietaryInfo__dietary-icons">
          {isVegetarian && (
            <DietaryIcon
              size={size}
              variant="vegetarian"
              data-testid="PriceAndDietaryInfo-Vegetarian"
            />
          )}
          {isVegan && (
            <DietaryIcon variant="vegan" data-testid="PriceAndDietaryInfo-Vegan" size={size} />
          )}
          {isGlutenFree && (
            <DietaryIcon
              size={size}
              variant="glutenFree"
              data-testid="PriceAndDietaryInfo-GlutenFree"
            />
          )}
        </div>
      )}
    </div>
  );
};
export default PriceAndDietaryInfo;
