import {useQuery} from 'react-query';

import MenuDetails from '../../components/MenuDetails';
import getCorrectText from '../../utils/get-correct-text';
import {withRQClient} from '../../utils/withRQClient';
import withWebComponent from '../../utils/withWebComponent';
import AEMContext from '../contexts/AEMContext';

import type {IMenuDetails} from '../../components/MenuDetails';
import type {AEMAuthorContextType} from '../contexts/AEMContext';

export type MenuDetailsWebComponentProps = AEMAuthorContextType & {
  apiUrl: string;
  id: string;
  pagePath: string;
  menuNameOverride?: string;
  menuDescriptionOverride?: string;
};

export type MenuDetailsConnectorProps = Omit<MenuDetailsWebComponentProps, 'apiUrl'>;

export const MenuDetailsConnector = ({
  id,
  idType,
  pagePath,
  menuNameOverride,
  menuDescriptionOverride,
  brandId,
  menuItemHighlightText,
  smartChefUrl
}: MenuDetailsConnectorProps) => {
  const url = `?id=${id}&websitePageUrlPath=${pagePath}&idType=${idType}`;

  const {isLoading, error, data} = useQuery<IMenuDetails>(url);

  if (isLoading) return <div>Loading...</div>;
  if (error) return <div>Error happened</div>;
  if (!data) return <div>No data</div>;

  const guestFacingName = getCorrectText(data.guestFacingName, menuNameOverride);
  const guestFacingDescriptionDigital = getCorrectText(
    data.guestFacingDescriptionDigital,
    menuDescriptionOverride
  );

  return (
    <AEMContext.Provider value={{idType, brandId, menuItemHighlightText, smartChefUrl}}>
      <MenuDetails
        {...data}
        guestFacingName={guestFacingName}
        guestFacingDescriptionDigital={guestFacingDescriptionDigital}
      />
    </AEMContext.Provider>
  );
};

export default withWebComponent(
  withRQClient<MenuDetailsWebComponentProps>(MenuDetailsConnector),
  'mab-menu',
  {
    id: 'string',
    apiUrl: 'string',
    idType: 'string',
    pagePath: 'string',
    menuNameOverride: 'string',
    menuDescriptionOverride: 'string',
    brandId: 'string',
    menuItemHighlightText: 'string',
    smartChefUrl: 'string'
  }
);
