import './Abv.scss';
import Text from './../Text';

export type AbvProps = {
  abv: string;
  isFeaturedItem?: boolean;
  isInModal?: boolean;
  'data-testid'?: string;
};

const Abv = ({
  abv,
  isFeaturedItem = false,
  isInModal = false,
  'data-testid': dataTestId
}: AbvProps) => {
  return (
    <Text
      font="secondary"
      className={`Abv__text${isFeaturedItem ? ' Abv__featured' : ''} ${
        isInModal ? ' Abv__text-modal' : ''
      }`}
      data-testid={dataTestId || 'Abv'}>
      {abv}% Vol.
    </Text>
  );
};

export default Abv;
