import './Nutrition.scss';
import Text from './../Text';
export type NutritionProps = {
  energyKcalPerPortion: string;
  isFeaturedItem?: boolean;
  isInModal?: boolean;
  withComma?: boolean;
  'data-testid'?: string;
};

const Nutrition = ({
  energyKcalPerPortion,
  isFeaturedItem = false,
  isInModal = false,
  withComma = false,
  'data-testid': dataTestId
}: NutritionProps) => {
  const energyKcalFormatted = Math.round(
    Number(energyKcalPerPortion.replace(/,/g, ''))
  ).toLocaleString();

  return (
    <Text
      font="secondary"
      className={`Nutrition__energyKcalPerPortion${isFeaturedItem ? ' Nutrition__featured' : ''} ${
        isInModal ? ' Nutrition__energyKcalPerPortion-modal' : ''
      }`}
      data-testid={dataTestId || 'Nutrition'}>
      {energyKcalFormatted} Kcal{withComma ? ',' : ''}
    </Text>
  );
};

export default Nutrition;
