import {QueryClient, QueryClientProvider} from 'react-query';

const queryClient = (baseUrl: string) =>
  new QueryClient({
    defaultOptions: {
      queries: {
        queryFn: async ({queryKey: [url]}) => {
          if (typeof url === 'string') {
            const response = await fetch(`${baseUrl}/${url}`, {
              method: 'GET',
              headers: {
                'Content-type': 'application/json; charset=UTF-8',
                'x-apikey': process.env.API_KEY
              } as HeadersInit
            });
            if (response.ok) {
              return response.json();
            }
            throw new Error('Something went wrong');
          }
          throw new Error('Invalid QueryKey');
        }
      }
    }
  });

type BaseApiProps = {
  apiUrl: string;
};

// HOC function that provides React Query client to children
export function withRQClient<T extends BaseApiProps>(WrappedComponent: React.ComponentType<T>) {
  // eslint-disable-next-line react/display-name
  return ({apiUrl, ...rest}: T) => (
    <QueryClientProvider client={queryClient(apiUrl)}>
      <WrappedComponent {...(rest as T)} />
    </QueryClientProvider>
  );
}
