import type {MenuItemProps} from '../../components/MenuItem/types';

type UseNutritionResult = {
  energyKcalPerPortion: string | null;
  withNutrition: boolean;
};

export const useNutrition = (
  isGri: boolean,
  portions: MenuItemProps['portions']
): UseNutritionResult => {
  const energyKcalPerPortion =
    !isGri && portions[0]?.nutrition ? portions[0].nutrition?.energyKcalPerPortion : null;

  const withNutrition = Boolean(energyKcalPerPortion && !isGri);

  return {energyKcalPerPortion, withNutrition};
};
