import Text from '../Text';

import type {FC, ReactElement} from 'react';
import './MenuActionButton.scss';
export interface IMenuActionButton {
  icon?: ReactElement;
  text: string;
  className?: string;
  href?: string;
  target?: string;
  variant?: 'medium' | 'small';
}

const MenuActionButton: FC<IMenuActionButton> = ({
  icon,
  text,
  className,
  href = '',
  target,
  variant = 'medium'
}) => {
  const Button = href ? 'a' : 'button';
  return (
    <Button
      href={href}
      className={`MenuActionButton__container MenuActionButton__variant__${variant} ${className}`}
      target={target}>
      {icon}
      <Text font="primary" className={`MenuActionButton__text MenuActionButton__text__${variant}`}>
        {text}
      </Text>
    </Button>
  );
};

export default MenuActionButton;
