import type {FC, ReactNode} from 'react';
import './Text.scss';
interface IText {
  font?: 'primary' | 'secondary';
  as?: 'span' | 'p' | 'h1' | 'h2' | 'h3' | 'h4';
  className?: string;
  children: string | ReactNode | ReactNode[];
}
const Text: FC<IText> = ({font = 'primary', as = 'span', className, children, ...props}) => {
  const Tag = as || 'span';
  return (
    <Tag {...props} className={`Text Text__${font} ${className ? className : ''}`}>
      {children}
    </Tag>
  );
};

export default Text;
