import {useContext} from 'react';

import AEMContext from '../../../connectors/contexts/AEMContext';
import {useNutrition} from '../../../hooks';
import Abv from '../../Abv';
import {InfoIcon} from '../../Icons';
import MenuActionButton from '../../MenuActionButton';
import Featured from '../../MenuItem/Featured';
import './MenuItemModalContent.scss';
import PriceAndDietaryInfo from '../../MenuItem/PriceAndDietaryInfo';
import Nutrition from '../../Nutrition';
import Text from '../../Text';

import type {MenuItemProps} from '../../MenuItem/types';

export type MenuItemModalContentProps = {
  item: MenuItemProps;
};

const MenuItemModalContent: React.FC<MenuItemModalContentProps> = ({item}) => {
  const {energyKcalPerPortion, withNutrition} = useNutrition(item.isGri, item.portions);
  const {smartChefUrl} = useContext(AEMContext);

  return (
    <div className="MenuItemModalContent__box" data-testid="MenuItemModalContent-Box">
      <div
        className="MenuItemModalContent__name-description-box"
        data-testid="MenuItemModalContent-NameAndDescription">
        <Text className="MenuItemModalContent__name" as="h2">
          {item.guestFacingName}
        </Text>
        <Text className="MenuItemModalContent__description" as="p" font="secondary">
          {item.guestFacingDescriptionDigital}
        </Text>
      </div>
      <div className="MenuItemModalContent__nutrition-and-abv">
        {withNutrition && energyKcalPerPortion && (
          <div
            className="MenuItemModalContent__nutrition"
            data-testid="MenuItemModalContent-Nutrition">
            <Nutrition
              energyKcalPerPortion={energyKcalPerPortion}
              isInModal={true}
              withComma={Boolean(item.abv)}
            />
          </div>
        )}
        {item.abv && (
          <div className="MenuItemModalContent__abv" data-testid="MenuItemModalContent-Abv">
            <Abv abv={item.abv} isInModal={true} />
          </div>
        )}
      </div>
      <div
        className="MenuItemModalContent__price-and-dietary-info-featured"
        data-testid="MenuItemModalContent-PriceAndDietaryInfoFeatured">
        <PriceAndDietaryInfo
          isInModal={true}
          isVegan={item.claims.madeWithVeganIngs}
          isVegetarian={item.claims.madeWithVegetarianIngs}
        />
        {item.isFeaturedItem && (
          <div>
            <Featured isInModal={true} />
          </div>
        )}
      </div>
      <div
        className="MenuItemModalContent__nutrition-and-allergy-info"
        data-testid="MenuItemModalContent-NutritionAndAllergyInfo">
        <MenuActionButton
          href={smartChefUrl}
          text="Nutrition & Allergy Info"
          icon={<InfoIcon className="MenuItemModalContent__nutrition-icon" />}
          target="_blank"
          variant="medium"
          className="MenuItemModalContent__nutrition-button"
        />
      </div>
    </div>
  );
};

export default MenuItemModalContent;
