export type WindowWithDataLayer = Window & {
  dataLayer: Record<string, unknown>[];
};

export declare const window: WindowWithDataLayer;

export enum PageType {
  VIEW_MENU = 'View-only Menus'
}
export enum EventType {
  PAGE_VIEW = 'pageView'
}

export enum LoginStatus {
  LOGGED_OUT = 'Logged-out',
  LOGGED_IN = 'Logged-in'
}

export enum BrandType {
  HARD = 'Hard Brand',
  SOFT = 'Soft Brand'
}

export enum PageHierarchy {
  OUTLET = 'Outlet Page',
  BRAND = 'Brand Page'
}

export type GTMArguments = {
  page_type: PageType;
  page_sub_type: string;
  brand: string;
  brand_type: BrandType;
  login_status: LoginStatus;
  page_hierarchy: string;
  outlet_name?: string;
};

export const isCookiePresent = (name: string): boolean => {
  const value = '; ' + document.cookie;
  const parts = value.split('; ' + name + '=');
  return parts.length === 2;
};

export const pushGTMEvent = (event: EventType, args: GTMArguments) => {
  const isGTMPresent = window.dataLayer !== undefined;

  // when script is loaded after this call
  window.dataLayer = window.dataLayer || [];

  if (!isGTMPresent) {
    console.warn('Could not find dataLayer object on window. Using an empty array');
  }

  const data = {
    event,
    ...args
  };
  window.dataLayer.push(data);
};
