import './DietaryIcon.scss';
import Text from './../Text';

export type DietaryIconProps = {
  variant: 'vegan' | 'vegetarian' | 'glutenFree';
  size?: 'small' | 'large';
  'data-testid'?: string;
};

const DietaryIcon = ({variant, size = 'small', 'data-testid': dataTestId}: DietaryIconProps) => {
  const isVegan = variant === 'vegan';
  const isVegetarian = variant === 'vegetarian';
  const isGlutenFree = variant === 'glutenFree';

  if (isVegetarian) {
    return (
      <div className="DietaryIcon__box">
        <Text
          font="secondary"
          className={`DietaryIcon__icon DietaryIcon__icon-${size} DietaryIcon__vegetarian`}
          aria-label="Vegetarian"
          data-testid={dataTestId || 'Vegetarian'}>
          v
        </Text>
      </div>
    );
  }
  if (isVegan) {
    return (
      <div className="DietaryIcon__box">
        <Text
          font="secondary"
          className={`DietaryIcon__icon DietaryIcon__icon-${size} DietaryIcon__vegan`}
          aria-label="Vegan"
          data-testid={dataTestId || 'Vegan'}>
          ve
        </Text>
      </div>
    );
  }
  if (isGlutenFree) {
    return (
      <div className="DietaryIcon__box">
        <Text
          font="secondary"
          className={`DietaryIcon__icon DietaryIcon__icon-${size} DietaryIcon__gluten-free`}
          aria-label="Gluten free"
          data-testid={dataTestId || 'GlutenFree'}>
          gf
        </Text>
      </div>
    );
  }
  return null;
};

export default DietaryIcon;
