import './MenuDetails.scss';
import {useCallback, useContext, useEffect} from 'react';

import {IdTypeEnum} from '../../assets/types/menu';
import AEMContext from '../../connectors/contexts/AEMContext';
import {getOutletName, getBrandType, getBunId} from '../../utils/estate-helpers';
import {
  EventType,
  isCookiePresent,
  LoginStatus,
  PageHierarchy,
  PageType,
  pushGTMEvent
} from '../../utils/gtm';
import MenuDietaryInfo from '../MenuDietaryInfo';
import MenuFooter from '../MenuFooter';
import MenuHeader from '../MenuHeader';
import MenuSection from '../MenuSection';

import type {GTMArguments} from '../../utils/gtm';
import type {IMenuSection} from '../MenuSection/types';
import type {FC} from 'react';

export interface IMenuDetails {
  menuId: string;
  name: string;
  guestFacingName: string;
  menuCopyDigitalFooter: string;
  guestFacingDescriptionDigital: string;
  sections?: IMenuSection[];
}
const MenuDetails: FC<IMenuDetails> = ({
  menuId,
  name,
  guestFacingName,
  menuCopyDigitalFooter,
  guestFacingDescriptionDigital,
  // menuCopyCaption, // footer title
  sections = []
}) => {
  const {idType, brandId} = useContext(AEMContext);

  const pushGTM = useCallback(() => {
    const b2cTokenExists = isCookiePresent('b2c_token');
    let gtmData: GTMArguments;
    const basicGtmData = {
      page_type: PageType.VIEW_MENU,
      page_sub_type: guestFacingName,
      brand: brandId,
      bun_id: getBunId(),
      brand_type: getBrandType(),
      login_status: b2cTokenExists ? LoginStatus.LOGGED_IN : LoginStatus.LOGGED_OUT
    };
    if (idType === IdTypeEnum.BRAND) {
      gtmData = {
        ...basicGtmData,
        page_hierarchy: PageHierarchy.BRAND
      };
    } else {
      gtmData = {
        ...basicGtmData,
        page_hierarchy: PageHierarchy.OUTLET,
        outlet_name: getOutletName()
      };
    }

    pushGTMEvent(EventType.PAGE_VIEW, gtmData);
  }, [brandId, guestFacingName, idType]);

  useEffect(() => {
    if (guestFacingName) {
      pushGTM();
    }
  }, [guestFacingName, pushGTM]);

  return (
    <article key={`${menuId}-${name}`} className="MenuDetails__container">
      <div className="MenuDetails__wrapper">
        <MenuHeader
          guestFacingName={guestFacingName}
          guestFacingDescriptionDigital={guestFacingDescriptionDigital}
        />
      </div>
      <div className="MenuDetails__wrapper">
        <div className="MenuDetails__dietary-info__wrapper">
          <MenuDietaryInfo />
        </div>
        <section className="MenuDetails__sections__wrapper">
          {sections.map((section) => {
            return <MenuSection key={section.sectionId} {...section} />;
          })}
        </section>
      </div>

      <div className="MenuDetails__footer__wrapper">
        <MenuFooter menuCopyDigitalFooter={menuCopyDigitalFooter} />
      </div>
    </article>
  );
};

export default MenuDetails;
