import {useContext} from 'react';

import AEMContext from '../../../connectors/contexts/AEMContext';
import {FeaturedIcon} from '../../Icons';
import './Featured.scss';
import Text from '../../Text';

export type FeaturedProps = {
  isInModal?: boolean;
  'data-testid'?: string;
};

const Featured = ({isInModal = false, 'data-testid': dataTestId}: FeaturedProps) => {
  const {menuItemHighlightText} = useContext(AEMContext);
  return (
    <div
      className={isInModal ? 'Featured__box Featured__modal' : 'Featured__box'}
      data-testid={dataTestId || 'Featured-Box'}>
      <Text
        font="secondary"
        className={isInModal ? 'Featured__text-modal' : 'Featured__text'}
        data-testid="Featured-Text">
        {menuItemHighlightText}
      </Text>
      <FeaturedIcon className="Featured__icon" />
    </div>
  );
};

export default Featured;
