import MenuItem from '../MenuItem';

import Text from './../Text';

import type {IMenuSubSection} from '../MenuSection/types';
import type {FC} from 'react';
import './MenuSubSection.scss';

const MenuSubSection: FC<IMenuSubSection> = ({
  menuCopyHeader,
  menuItems = [],
  isSubSection = false,
  menuCopyCaption
}) => {
  return (
    <section
      className={`MenuSubSection__container ${isSubSection && 'MenuSubSection__container__subsection'}`}>
      <div className="MenuSubSection__heading-and-caption">
        <Text
          as="h2"
          className={`MenuSubSection__title ${isSubSection && 'MenuSubSection__title__subsection'}`}>
          {menuCopyHeader}
        </Text>
        {menuCopyCaption && (
          <Text as="p" className="MenuSubSection__caption" font="secondary">
            {menuCopyCaption}
          </Text>
        )}
      </div>
      <div className="MenuSubSection__items-grid">
        {menuItems.map((menuItem) => (
          <MenuItem key={menuItem.menuItemId} {...menuItem} />
        ))}
      </div>
    </section>
  );
};
export default MenuSubSection;
